





































































import Vue, { PropType } from 'vue';

import { Product } from '@/models/product';

type VuetifyFormElement = Vue & { validate(): void };
// TODO: common validation rules
const required = (v: string) => !!v || 'Field is required';

export default Vue.extend({
	props: {
		product: Object as PropType<Product>,
		disabled: Boolean,
		isEditableMode: Boolean,
	},
	data: () => ({
		valid: true,
		validationRules: [required],
		//
		title: '',
		description: '',
		count: 0,
		price: 0,
	}),
	computed: {
		dynamicFormTitleText() {
			const textId = this.isEditableMode ? 'products.edit' : 'products.create';

			return this.$t(textId);
		},
		dynamicSubmitButtonText() {
			const textId = this.isEditableMode ? 'products.update' : 'products.save';

			return this.$t(textId);
		},
	},
	watch: {
		product(v: Product) {
			this.title = v.title;
			this.description = v.description;
			this.count = v.count;
			this.price = v.price;
		},
	},
	methods: {
		triggerValidation(): void {
			(this.$refs.form as VuetifyFormElement).validate();
		},
		handleFormSubmit() {
			this.triggerValidation();

			if (!this.valid) {
				return;
			}

			this.$emit('submit', {
				title: this.title,
				description: this.description,
				count: this.count,
				price: this.price,
			});
		},
		handleFormReset() {
			this.title = '';
			this.description = '';
			this.count = 0;
			this.price = 0;
		},
	},
});
